import React, { useState, useEffect } from 'react';
import { 
  OttobockOrder, 
  OttobockOrderFormData, 
  OttobockProductType,
  OttobockDiagnosis,
  OttobockSide,
  OttobockOrderType,
  OttobockOrderStatus,
  OttobockServiceType,
  OttobockMaterial,
  OttobockFitting
} from '../types/ottobockOrder';
import { PersonalFile } from '../types/personalFile';
import OttobockMaterialsTable from './OttobockMaterialsTable';
import OttobockFittingsManager from './OttobockFittingsManager';
import { X, Save, Plus, Trash2 } from 'lucide-react';

interface OttobockOrderFormProps {
  order?: OttobockOrder;
  personalFile?: PersonalFile;
  onSave: (order: OttobockOrder) => void;
  onCancel: () => void;
  productTypes: OttobockProductType[];
  diagnoses: OttobockDiagnosis[];
  materialUnits: string[];
  defaultMaterials: OttobockMaterial[];
}

const OttobockOrderForm: React.FC<OttobockOrderFormProps> = ({
  order,
  personalFile,
  onSave,
  onCancel,
  productTypes,
  diagnoses,
  materialUnits,
  defaultMaterials
}) => {
  const [formData, setFormData] = useState<OttobockOrderFormData>({
    productType: ' ',
    diagnosis: '  ',
    side: '',
    isHospitalized: false,
    orderType: '',
    materials: defaultMaterials,
    status: '',
    urgencyReason: '',
    serviceType: '',
    orderCost: 0,
    fittings: [],
    notes: ''
  });

  const [errors, setErrors] = useState<Record<string, string>>({});

  useEffect(() => {
    if (order) {
      setFormData({
        productType: order.productType,
        diagnosis: order.diagnosis,
        side: order.side,
        isHospitalized: order.isHospitalized,
        orderType: order.orderType,
        materials: order.materials,
        status: order.status,
        urgencyReason: order.urgencyReason || '',
        serviceType: order.serviceType,
        orderCost: order.orderCost,
        fittings: order.fittings,
        notes: order.notes || ''
      });
    }
  }, [order]);

  const validateForm = () => {
    const newErrors: Record<string, string> = {};

    if (!formData.productType) newErrors.productType = '  ';
    if (!formData.diagnosis) newErrors.diagnosis = ' ';
    if (!formData.side) newErrors.side = '  ';
    if (!formData.orderType) newErrors.orderType = '  ';
    if (!formData.status) newErrors.status = '  ';
    if (!formData.serviceType) newErrors.serviceType = '  ';
    
    if (formData.status === '' && !formData.urgencyReason) {
      newErrors.urgencyReason = '     ';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleInputChange = (field: keyof OttobockOrderFormData, value: string | number | boolean | OttobockMaterial[] | OttobockFitting[]) => {
    setFormData(prev => ({ ...prev, [field]: value }));
    
    //     
    if (errors[field]) {
      setErrors(prev => ({ ...prev, [field]: '' }));
    }
  };

  const handleAddFitting = () => {
    const fittingNumber = Math.min(formData.fittings.length + 1, 3) as 1 | 2 | 3;
    const newFitting: OttobockFitting = {
      id: `fit_${Date.now()}`,
      fittingNumber,
      callDate: new Date().toISOString().split('T')[0],
      appointmentDate: ''
    };
    
    setFormData(prev => ({
      ...prev,
      fittings: [...prev.fittings, newFitting]
    }));
  };

  const handleClearAllMaterials = () => {
    if (window.confirm(' ,     ?')) {
      setFormData(prev => ({
        ...prev,
        materials: []
      }));
    }
  };

  const handleClearAllFittings = () => {
    if (window.confirm(' ,     ?')) {
      setFormData(prev => ({
        ...prev,
        fittings: []
      }));
    }
  };

  const generateOrderNumber = () => {
    const currentYear = new Date().getFullYear();
    const randomNumber = Math.floor(Math.random() * 1000) + 1;
    return `-${currentYear}-${randomNumber.toString().padStart(3, '0')}`;
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    
    if (validateForm()) {
      const orderData: OttobockOrder = {
        id: order?.id || `OT${Date.now()}`,
        orderNumber: order?.orderNumber || generateOrderNumber(),
        orderDate: order?.orderDate || new Date().toISOString().split('T')[0],
        personalFileId: personalFile?.id || '',
        clientName: personalFile ? `${personalFile.lastName} ${personalFile.firstName} ${personalFile.middleName}` : '',
        disabilityGroup: personalFile?.disabilityGroup || '',
        disabilityCategory: personalFile?.disabilityCategory || '',
        productType: formData.productType,
        diagnosis: formData.diagnosis,
        side: formData.side,
        isHospitalized: formData.isHospitalized,
        orderType: formData.orderType,
        materials: formData.materials,
        status: formData.status,
        urgencyReason: formData.urgencyReason,
        serviceType: formData.serviceType,
        orderCost: formData.orderCost,
        fittings: formData.fittings,
        manufacturingDate: order?.manufacturingDate,
        issueDate: order?.issueDate,
        createdBy: order?.createdBy || '',
        createdAt: order?.createdAt || new Date().toISOString(),
        updatedAt: new Date().toISOString(),
        notes: formData.notes
      };

      onSave(orderData);
    }
  };

  return (
    <div className="bg-white rounded-lg shadow-lg p-6 max-w-6xl mx-auto">
      <div className="flex justify-between items-center mb-6">
        <h2 className="text-2xl font-bold text-gray-900">
          {order ? '    -' : '   -'}
        </h2>
        <button
          onClick={onCancel}
          className="p-2 text-gray-400 hover:text-gray-600"
        >
          <X className="w-5 h-5" />
        </button>
      </div>

      <form onSubmit={handleSubmit} className="space-y-8">
        {/*    */}
        {personalFile && (
          <div className="bg-gray-50 p-4 rounded-lg">
            <h3 className="text-lg font-semibold text-gray-900 mb-2">  </h3>
            <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
              <div>
                <p className="text-sm font-medium text-gray-700"></p>
                <p className="text-sm text-gray-900">
                  {personalFile.lastName} {personalFile.firstName} {personalFile.middleName}
                </p>
              </div>
              <div>
                <p className="text-sm font-medium text-gray-700"> </p>
                <p className="text-sm text-gray-900">{personalFile.disabilityGroup}</p>
              </div>
              <div>
                <p className="text-sm font-medium text-gray-700"></p>
                <p className="text-sm text-gray-900">{personalFile.pin}</p>
              </div>
            </div>
          </div>
        )}

        {/*     */}
        <div className="space-y-6">
          <h3 className="text-lg font-semibold text-gray-900">   </h3>
          
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                  <span className="text-red-500">*</span>
              </label>
              <select
                value={formData.productType}
                onChange={(e) => handleInputChange('productType', e.target.value as OttobockProductType)}
                className={`w-full px-3 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent ${
                  errors.productType ? 'border-red-500' : 'border-gray-300'
                }`}
              >
                {productTypes.map(type => (
                  <option key={type} value={type}>{type}</option>
                ))}
              </select>
              {errors.productType && <p className="text-red-500 text-xs mt-1">{errors.productType}</p>}
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                 <span className="text-red-500">*</span>
              </label>
              <select
                value={formData.diagnosis}
                onChange={(e) => handleInputChange('diagnosis', e.target.value as OttobockDiagnosis)}
                className={`w-full px-3 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent ${
                  errors.diagnosis ? 'border-red-500' : 'border-gray-300'
                }`}
              >
                {diagnoses.map(diagnosis => (
                  <option key={diagnosis} value={diagnosis}>{diagnosis}</option>
                ))}
              </select>
              {errors.diagnosis && <p className="text-red-500 text-xs mt-1">{errors.diagnosis}</p>}
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                  <span className="text-red-500">*</span>
              </label>
              <select
                value={formData.side}
                onChange={(e) => handleInputChange('side', e.target.value as OttobockSide)}
                className={`w-full px-3 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent ${
                  errors.side ? 'border-red-500' : 'border-gray-300'
                }`}
              >
                <option value=""></option>
                <option value=""></option>
              </select>
              {errors.side && <p className="text-red-500 text-xs mt-1">{errors.side}</p>}
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                
              </label>
              <select
                value={formData.isHospitalized ? '' : ''}
                onChange={(e) => handleInputChange('isHospitalized', e.target.value === '')}
                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              >
                <option value=""></option>
                <option value=""></option>
              </select>
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                  <span className="text-red-500">*</span>
              </label>
              <select
                value={formData.orderType}
                onChange={(e) => handleInputChange('orderType', e.target.value as OttobockOrderType)}
                className={`w-full px-3 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent ${
                  errors.orderType ? 'border-red-500' : 'border-gray-300'
                }`}
              >
                <option value=""></option>
                <option value=""></option>
              </select>
              {errors.orderType && <p className="text-red-500 text-xs mt-1">{errors.orderType}</p>}
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                  <span className="text-red-500">*</span>
              </label>
              <select
                value={formData.status}
                onChange={(e) => handleInputChange('status', e.target.value as OttobockOrderStatus)}
                className={`w-full px-3 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent ${
                  errors.status ? 'border-red-500' : 'border-gray-300'
                } ${formData.status === '' ? 'bg-red-50 border-red-300' : ''}`}
              >
                <option value=""></option>
                <option value=""></option>
              </select>
              {errors.status && <p className="text-red-500 text-xs mt-1">{errors.status}</p>}
            </div>
          </div>

          {/*   */}
          {formData.status === '' && (
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                  <span className="text-red-500">*</span>
              </label>
              <input
                type="text"
                value={formData.urgencyReason || ''}
                onChange={(e) => handleInputChange('urgencyReason', e.target.value)}
                className={`w-full px-3 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent ${
                  errors.urgencyReason ? 'border-red-500' : 'border-gray-300'
                }`}
                placeholder="  "
              />
              {errors.urgencyReason && <p className="text-red-500 text-xs mt-1">{errors.urgencyReason}</p>}
            </div>
          )}

          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                 <span className="text-red-500">*</span>
              </label>
              <select
                value={formData.serviceType}
                onChange={(e) => handleInputChange('serviceType', e.target.value as OttobockServiceType)}
                className={`w-full px-3 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent ${
                  errors.serviceType ? 'border-red-500' : 'border-gray-300'
                }`}
              >
                <option value=""></option>
                <option value=""></option>
              </select>
              {errors.serviceType && <p className="text-red-500 text-xs mt-1">{errors.serviceType}</p>}
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                  ()
              </label>
              <input
                type="number"
                min="0"
                step="0.01"
                value={formData.orderCost}
                onChange={(e) => handleInputChange('orderCost', parseFloat(e.target.value) || 0)}
                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                placeholder="  "
              />
            </div>
          </div>
        </div>

        {/*    */}
        <div className="space-y-4">
          <div className="flex justify-between items-center">
            <h3 className="text-lg font-semibold text-gray-900">
                 ({formData.materials.length})
            </h3>
            <div className="flex space-x-2">
              {formData.materials.length > 0 && (
                <button
                  type="button"
                  onClick={handleClearAllMaterials}
                  className="flex items-center space-x-2 px-3 py-2 bg-red-600 text-white rounded-lg hover:bg-red-700 transition-colors"
                >
                  <Trash2 className="w-4 h-4" />
                  <span> </span>
                </button>
              )}
            </div>
          </div>
          <OttobockMaterialsTable
            materials={formData.materials}
            onMaterialsChange={(materials) => handleInputChange('materials', materials)}
            availableUnits={materialUnits}
          />
        </div>

        {/*  */}
        <div className="space-y-4">
          <div className="flex justify-between items-center">
            <h3 className="text-lg font-semibold text-gray-900">
               ({formData.fittings.length}/3)
            </h3>
            <div className="flex space-x-2">
              {formData.fittings.length > 0 && (
                <button
                  type="button"
                  onClick={handleClearAllFittings}
                  className="flex items-center space-x-2 px-3 py-2 bg-red-600 text-white rounded-lg hover:bg-red-700 transition-colors"
                >
                  <Trash2 className="w-4 h-4" />
                  <span> </span>
                </button>
              )}
              <button
                type="button"
                onClick={handleAddFitting}
                disabled={formData.fittings.length >= 3}
                className={`flex items-center space-x-2 px-3 py-2 rounded-lg transition-colors ${
                  formData.fittings.length >= 3
                    ? 'bg-gray-300 text-gray-500 cursor-not-allowed'
                    : 'bg-blue-600 text-white hover:bg-blue-700'
                }`}
              >
                <Plus className="w-4 h-4" />
                <span>
                  {formData.fittings.length >= 3 ? ' 3 ' : ' '}
                </span>
              </button>
            </div>
          </div>
          <OttobockFittingsManager
            fittings={formData.fittings}
            onFittingsChange={(fittings) => handleInputChange('fittings', fittings)}
            onAddFitting={handleAddFitting}
          />
        </div>

        {/*   */}
        <div className="space-y-4">
          <h3 className="text-lg font-semibold text-gray-900"> </h3>
          
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                 
              </label>
              <input
                type="date"
                value={order?.manufacturingDate || ''}
                disabled
                className="w-full px-3 py-2 border border-gray-300 rounded-lg bg-gray-50 text-gray-500"
                placeholder="   "
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                 
              </label>
              <input
                type="date"
                value={order?.issueDate || ''}
                disabled
                className="w-full px-3 py-2 border border-gray-300 rounded-lg bg-gray-50 text-gray-500"
                placeholder="   "
              />
            </div>
          </div>
        </div>

        {/*  */}
        <div className="space-y-4">
          <h3 className="text-lg font-semibold text-gray-900"> </h3>
          
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              
            </label>
            <textarea
              value={formData.notes || ''}
              onChange={(e) => handleInputChange('notes', e.target.value)}
              className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              rows={3}
              placeholder="  "
            />
          </div>
        </div>

        {/*  */}
        <div className="flex justify-end space-x-4 pt-6 border-t">
          <button
            type="button"
            onClick={onCancel}
            className="px-6 py-2 border border-gray-300 text-gray-700 rounded-lg hover:bg-gray-50 transition-colors"
          >
            
          </button>
          <button
            type="submit"
            className="flex items-center space-x-2 px-6 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
          >
            <Save className="w-4 h-4" />
            <span>{order ? ' ' : ' '}</span>
          </button>
        </div>
      </form>
    </div>
  );
};

export default OttobockOrderForm;
